
import XHeader from 'vux/src/components/x-header/index.vue'

import { mapGetters } from "vuex";

import scroll from "../../scroll";
export default {
components: {
XHeader,
scroll

},
computed: {
...mapGetters([
"branchLessons",
"branchLessonsTotal",
"lessonBranchScrollTop"
]),
pullDownRefreshObj: function() {
return this.pullDownRefresh
? {
threshold: parseInt(this.pullDownRefreshThreshold),
stop: parseInt(this.pullDownRefreshStop),
text: "刷新完成"
}
: false;
},
pullUpLoadObj: function() {
return this.pullUpLoad
? {
threshold: parseInt(this.pullUpLoadThreshold),
txt: {
more: this.pullUpLoadMoreTxt,
noMore: this.pullUpLoadNoMoreTxt
}
}
: false;
}
},
created() {



},
mounted() {




},
activated() {
let time = window.sessionStorage.getItem("lesson_time");
let start = window.sessionStorage.getItem("lesson_start");
let end = window.sessionStorage.getItem("lesson_end");
this.$store.dispatch("getBranchLessons", {
department_id: this.$route.params.branchid,
time: time,
start: start,
end: end
});
},
deactivated() {
this.branchlessons = [];
},
watch: {
pullDownRefreshObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullDown();
} else {
scroll.closePullDown();
}
},
deep: true
},
pullUpLoadObj: {
handler(val) {
const scroll = this.$refs.scroll.scroll;
if (val) {
scroll.openPullUp();
} else {
scroll.closePullUp();
}
},
deep: true
},
startY() {
this.rebuildScroll();
},
branchLessons: function(Value) {
this.branchlessons = Value;
}

















},
data() {
return {
pullDownRefresh: true,
pullDownRefreshThreshold: 90,
pullDownRefreshStop: 40,
pullDownRefreshText: "刷新完成",
pullUpLoad: true,
pullUpLoadThreshold: 0,
pullUpLoadMoreTxt: "加载更多",
pullUpLoadNoMoreTxt: "没有更多数据了",
startY: 0,
scrollToX: 0,
scrollToY: -200,
scrollToTime: 700,
scrollToEasing: "bounce",
scrollToEasingOptions: ["bounce", "swipe", "swipeBounce"],
num: 10,
page: 1,



normalImg: require("./shyk_bg.png"),
branchlessons: []
};
},
methods: {
scrollTo() {
this.$refs.scroll.scrollTo(
this.scrollToX,
this.scrollToY,
this.scrollToTime,
ease[this.scrollToEasing]
);
},
onPullingDown() {
let time = window.sessionStorage.getItem("lesson_time");
let start = window.sessionStorage.getItem("lesson_start");
let end = window.sessionStorage.getItem("lesson_end");
console.log("pulling down and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}

this.$store.dispatch("getBranchLessons", {
department_id: this.$route.params.branchid,
time: time,
start: start,
end: end
});
this.$refs.scroll.forceUpdate();
this.page = 1;
}, 2000);
},
onPullingUp() {
let time = window.sessionStorage.getItem("lesson_time");
let start = window.sessionStorage.getItem("lesson_start");
let end = window.sessionStorage.getItem("lesson_end");
console.log("pulling up and load data");
setTimeout(() => {
if (this._isDestroyed) {
return;
}
this.page++;
this.$store
.dispatch("getBranchLessons", {
department_id: this.$route.params.branchid,
page: this.page,
time: time,
start: start,
end: end
})
.then(() => {
if (this.branchlessons.length >= this.branchLessonsTotal) {
this.$refs.scroll.forceUpdate();
}
});
}, 1500);
},

rebuildScroll() {
Vue.nextTick(() => {
this.$refs.scroll.destroy();
this.$refs.scroll.initScroll();
});
}


































},
filters: {
formatCate: function(value) {
if (value === 1) {
return "党员大会";
} else if (value === 2) {
return "支部委会";
} else if (value === 3) {
return "党小组会议";
} else if (value === 4) {
return "组织生活会";
}
}
}
};



